.session-tracker {
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: white;
}

.session-tracker-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
  flex-wrap: wrap;
  gap: 20px;
}

.session-tracker-header h2 {
  margin: 0;
  color: #fff;
  font-size: 2rem;
}

.view-tabs {
  display: flex;
  gap: 10px;
  background: rgba(255, 255, 255, 0.1);
  padding: 5px;
  border-radius: 25px;
}

.view-tabs button {
  padding: 10px 20px;
  border: none;
  background: transparent;
  color: rgba(255, 255, 255, 0.7);
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 500;
}

.view-tabs button:hover {
  background: rgba(255, 255, 255, 0.2);
  color: white;
}

.view-tabs button.active {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.3);
}

/* Overview Section */
.overview-section {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  margin-bottom: 30px;
}

.stat-card {
  background: rgba(255, 255, 255, 0.1);
  padding: 25px;
  border-radius: 15px;
  text-align: center;
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: transform 0.3s ease;
}

.stat-card:hover {
  transform: translateY(-5px);
}

.stat-card h3 {
  margin: 0 0 10px 0;
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.8);
  text-transform: uppercase;
  letter-spacing: 1px;
}

.stat-value {
  font-size: 2.5rem;
  font-weight: bold;
  margin-bottom: 5px;
}

.stat-label {
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.7);
}

/* Progress Chart */
.progress-chart {
  background: rgba(255, 255, 255, 0.05);
  padding: 25px;
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.progress-chart h3 {
  margin: 0 0 20px 0;
  color: white;
}

.chart-container {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.progress-bar {
  display: flex;
  align-items: center;
  gap: 15px;
}

.session-info {
  min-width: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.9rem;
}

.session-number {
  color: rgba(255, 255, 255, 0.7);
}

.session-score {
  font-weight: bold;
}

.progress-bar-container {
  flex: 1;
  height: 10px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 5px;
  overflow: hidden;
}

.progress-bar-fill {
  height: 100%;
  border-radius: 5px;
  transition: width 0.3s ease;
}

/* Breakdown Section */
.breakdown-section {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
}

.personality-breakdown,
.scenario-breakdown {
  background: rgba(255, 255, 255, 0.05);
  padding: 25px;
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.personality-breakdown h3,
.scenario-breakdown h3 {
  margin: 0 0 20px 0;
  color: white;
}

.breakdown-item {
  margin-bottom: 15px;
  padding: 15px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.breakdown-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5px;
}

.breakdown-name {
  font-weight: 500;
  color: white;
}

.breakdown-score {
  font-weight: bold;
}

.breakdown-details {
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.7);
}

/* Sessions List */
.sessions-list {
  background: rgba(255, 255, 255, 0.05);
  padding: 25px;
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.sessions-list h3 {
  margin: 0 0 20px 0;
  color: white;
}

.sessions-table {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.table-header {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 1fr 1fr 1fr 1fr;
  gap: 15px;
  padding: 15px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  font-weight: bold;
  color: white;
  font-size: 0.9rem;
}

.table-row {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 1fr 1fr 1fr 1fr;
  gap: 15px;
  padding: 15px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: background 0.3s ease;
  font-size: 0.9rem;
}

.table-row:hover {
  background: rgba(255, 255, 255, 0.1);
}

.session-status.completed {
  color: #4CAF50;
  font-weight: 500;
}

.session-status.active {
  color: #FF9800;
  font-weight: 500;
}

.session-status.abandoned {
  color: #F44336;
  font-weight: 500;
}

.session-recording {
  display: flex;
  align-items: center;
  justify-content: center;
}

.no-recording {
  color: rgba(255, 255, 255, 0.5);
  font-style: italic;
  font-size: 0.8rem;
}

/* Analytics Section */
.analytics-section {
  background: rgba(255, 255, 255, 0.05);
  padding: 25px;
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.analytics-section h3 {
  margin: 0 0 20px 0;
  color: white;
}

.analytics-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
}

.analytics-card {
  background: rgba(255, 255, 255, 0.05);
  padding: 20px;
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.analytics-card h4 {
  margin: 0 0 15px 0;
  color: white;
}

.trend-analysis p,
.strengths-analysis div {
  margin: 10px 0;
  color: rgba(255, 255, 255, 0.9);
}

.strength {
  color: #4CAF50;
}

.improvement {
  color: #FF9800;
}

/* Loading and Error States */
.session-tracker.loading,
.session-tracker.error {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 200px;
  font-size: 1.2rem;
  color: rgba(255, 255, 255, 0.8);
}

.session-tracker.error {
  color: #F44336;
}

/* Responsive Design */
@media (max-width: 768px) {
  .session-tracker {
    padding: 15px;
    margin: 10px;
  }

  .session-tracker-header {
    flex-direction: column;
    align-items: stretch;
  }

  .session-tracker-header h2 {
    text-align: center;
    font-size: 1.5rem;
  }

  .view-tabs {
    justify-content: center;
  }

  .stats-grid {
    grid-template-columns: 1fr;
  }

  .breakdown-section {
    grid-template-columns: 1fr;
  }

  .table-header,
  .table-row {
    grid-template-columns: 1fr;
    gap: 5px;
  }

  .table-header span,
  .table-row span {
    padding: 5px 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  }

  .session-recording {
    justify-content: flex-start;
    margin-top: 5px;
  }

  .analytics-grid {
    grid-template-columns: 1fr;
  }
}
