/* Audio Player Styles */
.audio-player {
  margin: 10px 0;
  padding: 15px;
  background: #f8f9fa;
  border-radius: 8px;
  border: 1px solid #dee2e6;
}

.audio-player-compact {
  display: inline-flex;
  align-items: center;
  margin: 0 5px;
}

.audio-controls {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 10px;
}

.play-button {
  background: #007bff;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px;
  display: flex;
  align-items: center;
  gap: 5px;
  transition: background-color 0.2s;
}

.play-button:hover:not(:disabled) {
  background: #0056b3;
}

.play-button:disabled {
  background: #6c757d;
  cursor: not-allowed;
}

.play-button-compact {
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 5px;
  border-radius: 3px;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  transition: background-color 0.2s;
}

.play-button-compact:hover:not(:disabled) {
  background: #f0f0f0;
}

.play-button-compact:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.time-display {
  font-family: 'Courier New', monospace;
  font-size: 14px;
  color: #495057;
  min-width: 100px;
}

.progress-container {
  cursor: pointer;
  margin-top: 5px;
}

.progress-bar {
  width: 100%;
  height: 6px;
  background: #e9ecef;
  border-radius: 3px;
  overflow: hidden;
}

.progress-fill {
  height: 100%;
  background: #007bff;
  transition: width 0.1s ease;
}

.audio-error {
  color: #dc3545;
  font-size: 12px;
  margin-top: 5px;
  padding: 5px;
  background: #f8d7da;
  border: 1px solid #f5c6cb;
  border-radius: 3px;
}

/* Responsive Design */
@media (max-width: 768px) {
  .audio-player {
    padding: 10px;
  }
  
  .audio-controls {
    gap: 10px;
  }
  
  .play-button {
    padding: 8px 15px;
    font-size: 12px;
  }
  
  .time-display {
    font-size: 12px;
    min-width: 80px;
  }
}
