.admin-dashboard {
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 1rem;
  color: white;
}

.admin-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  background: rgba(255,255,255,0.1);
  padding: 1rem 2rem;
  border-radius: 15px;
  backdrop-filter: blur(10px);
}

.admin-header h1 {
  margin: 0;
  font-size: 2rem;
}

.logout-btn {
  background: rgba(255,255,255,0.2);
  border: 1px solid rgba(255,255,255,0.3);
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.logout-btn:hover {
  background: rgba(255,255,255,0.3);
}

.admin-tabs {
  display: flex;
  gap: 1rem;
  margin-bottom: 2rem;
}

.admin-tabs button {
  background: rgba(255,255,255,0.1);
  border: 1px solid rgba(255,255,255,0.2);
  color: white;
  padding: 0.75rem 1.5rem;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 1rem;
}

.admin-tabs button:hover {
  background: rgba(255,255,255,0.2);
  transform: translateY(-2px);
}

.admin-tabs button.active {
  background: rgba(255,255,255,0.3);
  border-color: rgba(255,255,255,0.5);
  transform: translateY(-2px);
  box-shadow: 0 4px 20px rgba(0,0,0,0.2);
}

.admin-content {
  background: rgba(255,255,255,0.1);
  border-radius: 15px;
  padding: 2rem;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255,255,255,0.2);
  min-height: 500px;
}

.loading {
  text-align: center;
  font-size: 1.2rem;
  padding: 2rem;
}

.tab-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}

.tab-header h2 {
  margin: 0;
  font-size: 1.5rem;
}

.add-btn {
  background: linear-gradient(135deg, #4ECDC4 0%, #36D1DC 100%);
  border: none;
  color: white;
  padding: 0.75rem 1.5rem;
  border-radius: 10px;
  cursor: pointer;
  font-size: 1rem;
  transition: all 0.3s ease;
}

.add-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 20px rgba(78, 205, 196, 0.3);
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background: rgba(255,255,255,0.1);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255,255,255,0.2);
  border-radius: 15px;
  padding: 2rem;
  width: 90%;
  max-width: 500px;
  color: white;
}

.modal-content h3 {
  margin-top: 0;
  margin-bottom: 1.5rem;
  text-align: center;
}

.modal-content form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.modal-content input,
.modal-content textarea,
.modal-content select {
  background: rgba(255,255,255,0.1);
  border: 1px solid rgba(255,255,255,0.3);
  color: white;
  padding: 0.75rem;
  border-radius: 8px;
  font-size: 1rem;
}

.modal-content input::placeholder,
.modal-content textarea::placeholder {
  color: rgba(255,255,255,0.7);
}

.modal-content textarea {
  min-height: 100px;
  resize: vertical;
}

.form-actions {
  display: flex;
  gap: 1rem;
  justify-content: center;
  margin-top: 1rem;
}

.form-actions button {
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  font-size: 1rem;
  transition: all 0.3s ease;
}

.form-actions button[type="submit"] {
  background: linear-gradient(135deg, #4ECDC4 0%, #36D1DC 100%);
  color: white;
}

.form-actions button[type="button"] {
  background: rgba(255,255,255,0.1);
  color: white;
  border: 1px solid rgba(255,255,255,0.3);
}

.form-actions button:hover {
  transform: translateY(-2px);
}

/* Users Tab */
.users-list {
  display: grid;
  gap: 1rem;
}

.user-card {
  background: rgba(255,255,255,0.05);
  border: 1px solid rgba(255,255,255,0.1);
  border-radius: 10px;
  padding: 1.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all 0.3s ease;
}

.user-card:hover {
  background: rgba(255,255,255,0.1);
  transform: translateY(-2px);
}

.user-info h4 {
  margin: 0 0 0.5rem 0;
  color: #4ECDC4;
}

.user-info p {
  margin: 0.25rem 0;
  opacity: 0.9;
}

.user-info small {
  opacity: 0.7;
}

.user-actions .delete-btn {
  background: rgba(255, 82, 82, 0.2);
  border: 1px solid rgba(255, 82, 82, 0.3);
  color: #ff5252;
  padding: 0.5rem;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.user-actions .delete-btn:hover:not(:disabled) {
  background: rgba(255, 82, 82, 0.3);
}

.user-actions .delete-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Personalities Tab */
.personalities-list {
  display: grid;
  gap: 1rem;
}

.personality-card {
  background: rgba(255,255,255,0.05);
  border: 1px solid rgba(255,255,255,0.1);
  border-radius: 10px;
  padding: 1.5rem;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  transition: all 0.3s ease;
}

.personality-card:hover {
  background: rgba(255,255,255,0.1);
  transform: translateY(-2px);
}

.personality-info h4 {
  margin: 0 0 0.5rem 0;
  color: #4ECDC4;
}

.personality-info p {
  margin: 0.5rem 0;
  opacity: 0.9;
}

.traits {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-top: 1rem;
}

.trait-tag {
  background: rgba(78, 205, 196, 0.2);
  color: #4ECDC4;
  padding: 0.25rem 0.75rem;
  border-radius: 15px;
  font-size: 0.85rem;
  border: 1px solid rgba(78, 205, 196, 0.3);
}

.personality-actions .delete-btn {
  background: rgba(255, 82, 82, 0.2);
  border: 1px solid rgba(255, 82, 82, 0.3);
  color: #ff5252;
  padding: 0.5rem;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.personality-actions .delete-btn:hover {
  background: rgba(255, 82, 82, 0.3);
}

/* Scenarios Tab */
.scenarios-list {
  display: grid;
  gap: 1rem;
}

.scenario-card {
  background: rgba(255,255,255,0.05);
  border: 1px solid rgba(255,255,255,0.1);
  border-radius: 10px;
  padding: 1.5rem;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  transition: all 0.3s ease;
}

.scenario-card:hover {
  background: rgba(255,255,255,0.1);
  transform: translateY(-2px);
}

.scenario-info h4 {
  margin: 0 0 0.5rem 0;
  color: #4ECDC4;
}

.scenario-info p {
  margin: 0.5rem 0;
  opacity: 0.9;
}

.issues {
  margin-top: 1rem;
}

.issue-tag {
  background: rgba(118, 75, 162, 0.2);
  color: #764ba2;
  padding: 0.25rem 0.75rem;
  border-radius: 15px;
  font-size: 0.85rem;
  border: 1px solid rgba(118, 75, 162, 0.3);
  margin-right: 0.5rem;
  margin-top: 0.5rem;
  display: inline-block;
}

.scenario-actions .delete-btn {
  background: rgba(255, 82, 82, 0.2);
  border: 1px solid rgba(255, 82, 82, 0.3);
  color: #ff5252;
  padding: 0.5rem;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.scenario-actions .delete-btn:hover {
  background: rgba(255, 82, 82, 0.3);
}

/* Analytics Tab */
.analytics-tab h2 {
  margin-bottom: 2rem;
  text-align: center;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1.5rem;
  margin-bottom: 3rem;
}

.stat-card {
  background: rgba(255,255,255,0.05);
  border: 1px solid rgba(255,255,255,0.1);
  border-radius: 10px;
  padding: 1.5rem;
  text-align: center;
  transition: all 0.3s ease;
}

.stat-card:hover {
  background: rgba(255,255,255,0.1);
  transform: translateY(-2px);
}

.stat-card h3 {
  margin: 0 0 1rem 0;
  opacity: 0.8;
  font-size: 1rem;
}

.stat-value {
  font-size: 2.5rem;
  font-weight: bold;
  color: #4ECDC4;
}

.breakdowns {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
}

.breakdown-section {
  background: rgba(255,255,255,0.05);
  border: 1px solid rgba(255,255,255,0.1);
  border-radius: 10px;
  padding: 1.5rem;
}

.breakdown-section h3 {
  margin: 0 0 1rem 0;
  color: #4ECDC4;
}

.breakdown-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.75rem 0;
  border-bottom: 1px solid rgba(255,255,255,0.1);
}

.breakdown-item:last-child {
  border-bottom: none;
}

.breakdown-item span:first-child {
  font-weight: 500;
}

.breakdown-item span:last-child {
  color: #4ECDC4;
  font-weight: bold;
}

/* Session Tracking Styles */
.sessions-tab {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  padding: 2rem;
  backdrop-filter: blur(10px);
}

.sessions-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  flex-wrap: wrap;
  gap: 1rem;
}

.sessions-header h2 {
  margin: 0;
  font-size: 2rem;
  color: white;
}

.view-tabs {
  display: flex;
  gap: 0.5rem;
  background: rgba(255, 255, 255, 0.1);
  padding: 0.3rem;
  border-radius: 15px;
}

.view-tabs button {
  padding: 0.6rem 1.2rem;
  border: none;
  background: transparent;
  color: rgba(255, 255, 255, 0.7);
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 0.9rem;
  font-weight: 500;
}

.view-tabs button:hover {
  background: rgba(255, 255, 255, 0.2);
  color: white;
}

.view-tabs button.active {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.3);
}

/* User Performance Overview */
.user-performance-overview {
  margin: 2rem 0;
  background: rgba(255, 255, 255, 0.05);
  padding: 1.5rem;
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.user-performance-overview h3 {
  margin: 0 0 1rem 0;
  color: white;
}

.user-performance-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
}

.user-performance-card {
  background: rgba(255, 255, 255, 0.1);
  padding: 1rem;
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  cursor: pointer;
  transition: all 0.3s ease;
}

.user-performance-card:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateY(-2px);
}

.user-name {
  font-weight: bold;
  margin-bottom: 0.5rem;
  color: white;
}

.user-stats {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
  font-size: 0.9rem;
}

.sessions-count {
  color: rgba(255, 255, 255, 0.8);
}

.avg-score {
  font-weight: bold;
}

.performance-label {
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.7);
}

/* Sessions Table */
.sessions-table {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  overflow: hidden;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.table-header {
  display: grid;
  grid-template-columns: 1.5fr 1fr 1fr 1fr 0.8fr 0.8fr 0.8fr 1fr;
  gap: 1rem;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.1);
  font-weight: bold;
  color: white;
  font-size: 0.9rem;
}

.table-row {
  display: grid;
  grid-template-columns: 1.5fr 1fr 1fr 1fr 0.8fr 0.8fr 0.8fr 1fr;
  gap: 1rem;
  padding: 1rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  transition: background 0.3s ease;
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.9);
}

.table-row:hover {
  background: rgba(255, 255, 255, 0.1);
}

.table-row:last-child {
  border-bottom: none;
}

.session-agent {
  font-weight: 500;
}

.session-status.completed {
  color: #4CAF50;
  font-weight: 500;
}

.session-status.active {
  color: #FF9800;
  font-weight: 500;
}

.session-status.abandoned {
  color: #F44336;
  font-weight: 500;
}

.session-recording {
  display: flex;
  align-items: center;
  justify-content: center;
}

.no-recording {
  color: rgba(255, 255, 255, 0.5);
  font-style: italic;
  font-size: 0.8rem;
}

/* User Details Section */
.user-details-section {
  background: rgba(255, 255, 255, 0.05);
  padding: 1.5rem;
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.user-details-section h3,
.user-details-section h4 {
  color: white;
  margin-bottom: 1rem;
}

.select-user-prompt {
  text-align: center;
  padding: 2rem;
  color: rgba(255, 255, 255, 0.8);
}

.user-selection {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  justify-content: center;
  margin-top: 1rem;
}

.user-select-btn {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.user-select-btn:hover {
  background: rgba(255, 255, 255, 0.2);
}

/* Enhanced Breakdown Styles */
.breakdown-section .breakdown-item {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr;
  gap: 1rem;
  align-items: center;
  padding: 0.8rem;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 8px;
  margin-bottom: 0.5rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.breakdown-name {
  font-weight: 500;
  color: white;
}

.breakdown-count {
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.9rem;
}

.breakdown-score {
  font-weight: bold;
  text-align: right;
}

/* Responsive Design for Sessions */
@media (max-width: 768px) {
  .admin-dashboard {
    padding: 0.5rem;
  }
  
  .admin-tabs {
    flex-wrap: wrap;
  }
  
  .admin-tabs button {
    flex: 1;
    min-width: 120px;
  }
  
  .breakdowns {
    grid-template-columns: 1fr;
  }
  
  .stats-grid {
    grid-template-columns: 1fr;
  }
  
  .user-card,
  .personality-card,
  .scenario-card {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
  }

  .sessions-header {
    flex-direction: column;
    align-items: stretch;
  }

  .sessions-header h2 {
    text-align: center;
    font-size: 1.5rem;
  }

  .view-tabs {
    justify-content: center;
  }

  .user-performance-grid {
    grid-template-columns: 1fr;
  }

  .table-header,
  .table-row {
    grid-template-columns: 1fr;
    gap: 0.5rem;
  }

  .table-header span,
  .table-row span {
    padding: 0.3rem 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  }

  .breakdown-section .breakdown-item {
    grid-template-columns: 1fr;
    text-align: center;
  }
}
